'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { motion } from 'framer-motion';

const links = [
    { href: '/', label: 'Home' },
    { href: '/about', label: 'About' },
    { href: '/team', label: 'Team' },
    { href: '/contact', label: 'Contact' },
];

export default function Navbar() {
    const pathname = usePathname();

    return (
        <nav className="fixed top-0 left-0 w-full z-50 px-6 py-4 flex justify-between items-center mix-blend-difference text-white">
            <Link href="/" className="text-2xl font-bold tracking-tighter">
                AGENCY
            </Link>

            <ul className="flex gap-8 items-center">
                {links.map((link) => (
                    <li key={link.href} className="relative">
                        <Link href={link.href} className="text-sm uppercase tracking-widest hover:text-gray-300 transition-colors">
                            {link.label}
                        </Link>
                        {pathname === link.href && (
                            <motion.div
                                layoutId="underline"
                                className="absolute left-0 top-full mt-1 w-full h-[1px] bg-white"
                            />
                        )}
                    </li>
                ))}
            </ul>
        </nav>
    );
}
